<template>
    <div>
        <el-row>
            <el-col class="search-bar">
                <el-input placeholder="请输入标题" class="w_200" icon="search" v-model="searchWord" :on-icon-click="search"></el-input>
                <div class="m_l_20">
                    <el-button @click="sortTable">{{orderBy == 1? '正': '倒'}}序排列</el-button>
                </div>
                <div class="m_l_20">
                    <el-button @click="addItem" icon='plus'>新建消息</el-button>
                </div>
                <div class="m_l_20">
                    <el-button @click="exportExc" :loading="exportLoading" icon='document'>导出Excel</el-button>
                </div>
            </el-col>
        </el-row>
        <el-row>
            <el-col>
                <el-table class="table" v-loading="listLoading" :data="tableData" border width="100%" :max-height="tableMaxHeight">
                    <el-table-column align="center" label="序号" width="80">
                        <template scope="scope">
                            {{(scope.$index+1) + pageSize * (currentPage-1)}}
                        </template>
                    </el-table-column>
                    <el-table-column align="center" prop="tittle" label="标题">
                    </el-table-column>
                    <el-table-column align="center" prop="content" label="内容">
                    </el-table-column>
                    <el-table-column align="center" prop="createAt.time" label="注册时间" width="150" :formatter="formatTime">
                    </el-table-column>
                    <el-table-column align="center" label="操作" width="120">
                        <template scope="scope">
                            <el-button size="small" type="danger" @click="deleteItem(scope.$index, scope.row)" icon='delete2'>删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>

        <el-row>
            <el-col>
                <div class="block pagination">
                    <el-pagination :current-page.sync="currentPage" @current-change="handleCurrentChange" :page-size="pageSize" layout="prev, pager, next, jumper" :page-count="total">
                    </el-pagination>
                </div>
            </el-col>
        </el-row>
    
        <el-dialog class="add add-dialog" title="推送消息" :visible.sync="addDialog" @close="resetFields('ruleForm')">
            <el-form :model="form" :rules="rules" ref="ruleForm">
                <el-form-item label="标题" prop="tittle">
                    <span>{{tittleNum}}</span>/20
                    <el-input type="text" :maxlength="20" placeholder="请输入标题" v-model="form.tittle">
                    </el-input>
                </el-form-item>
                <el-form-item label="正文：" prop="content">
                    <span>{{contentNum}}</span>/200
                    <el-input type="textarea" :maxlength="200" :rows="5" placeholder="请输入内容" v-model="form.content">
                    </el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="addDialog = false">取 消</el-button>
                <el-button type="primary" @click="addSave('ruleForm')">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            word: '',
            searchWord: '',
            orderBy: 2,
            listLoading: false,
            exportLoading: false,
            tableData: [],

            addDialog: false, //弹出框-新建
            form: {
                tittle: '',
                content: ''
            },
            // 分页
            currentPage: 1,
            pageSize: 20,
            total: 0,
            tableTop: 0,

            rules: {
                tittle: [
                    {required: true, message: '标题不能为空', trigger: 'blur'},
                    {max: 20, message: '限 20 个字符', trigger: 'blur' }],
                content: [
                    {required: true, message: '内容不能为空', trigger: 'blur'},
                    {max: 200, message: '限 200 个字符', trigger: 'blur' }]
            }
        }
    },
    computed: {
        tittleNum() {
            return this.form.tittle && this.form.tittle.length || 0
        },
        contentNum() {
            return this.form.content && this.form.content.length || 0
        }
    },
    props: ['API'],
    computed: {
        tableMaxHeight() {
            return (window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight || 0) - this.tableTop - 100
        }
    },
    methods: {
        search(ev) {
            this.word = this.searchWord
            this.currentPage = 1
            this.getList(this.word);
        },
        // 排序
        sortTable() {
            this.orderBy = this.orderBy == 1? 2: 1
            this.getList(this.word)
        },
        // 格式化
            // 格式化时间
            formatTime(row, column) {
                return this.$common.getNowTime(row.createAt && row.createAt.time)
            },
        // 查看-添加
        addItem() {
            this.form = {
                tittle: '',
                content: ''
            },
            this.addDialog = true
        },
        // 添加-保存
        addSave(formName){
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$confirm('是否确认添加?', '提示', {})
                        .then(() => {
                            var json = {
                                "head": {
                                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                                },
                                "body": {
                                   "content": this.form.content,
                                   "tittle": this.form.tittle
                                }
                            }
                            this.$common._post('MessageBase/add', json, this, data => {
                                this.addDialog = false
                                this.$message({
                                    type: 'success',
                                    message: '添加成功!'
                                })
                                this.getList()
                            })
                        }).catch(null)
                }
            })
        },
        // 删除
        deleteItem(index, row) {
            var json = {
                "head": {
                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                },
                "body": {
                   "id": row.id,
                   "tittle": row.tittle
                }
            }
            this.$common.deleteItem(this.API + 'MessageBase/delete', this, json)
        },
        // 重置表单
        resetFields(formName){
            this.$refs[formName].resetFields()
            this.form = {}
        },
        // 导出
        exportExc() {
            this.exportLoading = true
            var json = {
                "head": {
                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                },
                "body": { }
            }
            this.$http.post(this.API + 'exportExcelMessageBase', json).then(data=>{
                this.exportLoading = false
                var data = data.body
                if (data.body.result == '0') {
                    location.href = data.body.fileUrl
                } else {
                    this.$message(data.body.description)
                }
            })
        },
        handleCurrentChange(val) {
            this.currentPage = val;
            this.getList(this.word);
        },
        // 获取列表
        getList(tittle = ''){
            this.listLoading = true
            var json = {
                "head": {
                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                },
                "body": {
                    "pageIndex": this.currentPage,
                    "pageSize": this.pageSize,
                    "tittle": tittle,
                    "orderBy": this.orderBy,
                }
            }
            this.$common._post('MessageBase/list', json, this, data => {
                this.total = data.total
                this.tableData = data.list
                this.listLoading = false
            }, data=>{
                this.listLoading = false
                this.tableData = []
            })
        }
    },
    mounted(){
        if (!sessionStorage.getItem('zhizunbao_login')) {
            this.$message('请登录账号')
            this.$router.push('/')
            return
        }
        this.getList()
        this.tableTop = (document.querySelector('#main table').getBoundingClientRect().top)
    }
}
</script>

<style scoped lang="scss">

</style>



